    sloka: `అథ హైనం గార్హపత్యోఽనుశశాస పృథివ్యగ్నిరన్నమాదిత్య ఇతి య ఏష ఆదిత్యే పురుషో దృశ్యతే సోఽహమస్మి స ఏవాహమస్మీతి ||`,
