    sloka: `తే హోచురుపకోసలైషా సోమ్య తేఽస్మద్విద్యాత్మవిద్యా చాచార్యస్తు తే గతిం వక్తేత్యాజగామ హాస్యాచార్యస్తమాచార్యోఽభ్యువాదోపకోసల3 ఇతి ||`,
